<template>
  <div id="question">
    <head-top title="提问"></head-top>
    <div class="main" ref="main">
      <div class="questions" v-for="(item, i) in dataList" :key="i">
        <div class="time">{{item['CreateTime'] | formatDate(item['CreateTime']) }}</div>
        <div class="info">{{item['QContent']}}</div>
      </div>
      <r-lack-data v-if="!dataList || dataList.length === 0"></r-lack-data>
    </div>
    <div class="footer">
      <input type="text" :class="content.length===0 ? 'long':'short'" v-model="content" placeholder="请输入提问内容" @keyup.enter="dataAdd" />
      <div class="footer_btn" v-if="content.length > 0">
        <van-button size="small" type="primary" @click="dataAdd">发送</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import { emojValidator } from "@/utils/index";
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      dataList: [],
      content: ""
    };
  },
  created() { },
  mounted() {
    this.$nextTick(() => {
      this.getDataList();
    });
  },
  methods: {
    getDataList() {
      this.dataList = [];
      this.$mainHttp.question.getList({
        CIID: this.$store.getters.courseID,
        studentID: this.$store.getters.studentID,
        attribute: 2
      }).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.dataList.push(item);
          });
          setTimeout(() => {
            this.$refs["main"].scrollTop = this.$refs["main"].scrollHeight;
          }, 200);
        }
      }).catch(() => { });
    },
    dataAdd() {
      if (emojValidator(this.content)) {
        this.$toast("不支持表情字符");
        return;
      }
      this.$mainHttp.question.postDataSave({
        chID: this.$store.getters.course["CHID"],
        ciID: this.$store.getters.courseID,
        studentID: this.$store.getters.studentID,
        qContent: this.content
      }).then(res => {
        if (res.Result) {
          this.dataList.push({
            CreateTime: "/Date(" + new Date().getTime() + ")/",
            QContent: this.content
          });
          this.content = "";
          setTimeout(() => {
            this.$refs["main"].scrollTop = this.$refs["main"].scrollHeight;
          }, 200);
          // this.getDataList();
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => { });
    }
  },
  computed: {},
  watch: {}
};
</script>

<style lang='less' scoped>
@import url("./../../../styles/common/base.less");
#question {
  width: 100%;
  height: 100%;

  .main {
    // overflow: hidden;
    height: calc(100vh - 45px - 40px);
    overflow-y: auto;

    .questions {
      width: 92vw;
      padding: 5px 2vw;
      margin: 0 auto;
      margin-top: 10px;

      text-align: left;
      background: rgba(255, 255, 255, 1);
      border-radius: 3px;

      word-break: break-all;
      word-wrap: break-word;
      .time {
        font-size: @font-info;
        font-family: MicrosoftYaHei;
        color: rgba(102, 102, 102, 1);
        line-height: 2.5;
      }
      .info {
        padding: 05px 0;
        font-size: @font-info;
        line-height: 1.6;
        color: rgba(51, 51, 51, 1);
      }
      &:last-child {
        margin-bottom: 10px;
      }
    }
  }

  .footer {
    height: 40px;
    background: #fff;

    input {
      float: left;
      height: 30px;
      line-height: 30px;
      margin: 5px;
      padding-left: 5px;

      background: rgba(235, 240, 239, 1);
      border-radius: 3px;
      // margin: 0 auto;
      font-size: @font-sub-title;
      text-align: left;
    }
    .long {
      width: calc(100vw - 15px);
    }
    .short {
      width: calc(100vw - 15px - 70px);
    }

    .footer_btn {
      float: left;
      padding: 5px;
      .van-button {
        font-size: @font-info;
      }
    }
  }
}
</style>


